import React, { useEffect, useState } from 'react';
import { makeStyles } from '@mui/styles';
import axios from 'axios';
import {MY_FRIENDS, PROF} from '../../service/reducer/const';
import {Typography, IconButton, TextField, Link, Button} from '@mui/material';
import EditIcon from '@mui/icons-material/Edit';
import {useNavigate} from "react-router-dom";

const useStyles = makeStyles({
    friendsContainer: {
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
        paddingTop: '48px',
        width: '450px',
        margin: '0 auto',
        border: '2px solid pink',
        borderRadius: '20px',
        marginBottom: '10px',
        marginTop: '50px'
    },
    listItem: {
        display: 'flex',
        justifyContent: 'center',
        width: '15%',
        border: '2px solid pink',
        borderRadius: '20px',
        marginBottom: '10px',
    },
    buttonGroup: {
        display: 'flex',
        gap: '10px',
    },
    returnButton: {
        marginTop: '20px',
        background: 'pink',
        color: 'white',
        borderRadius: '20px',
        padding: '10px 20px',
        '&:hover': {
            background: 'lightpink',
        },
    },
});

function Profile() {
    const classes = useStyles();
    const [userData, setUserData] = useState(null);
    const [editMode, setEditMode] = useState(false);
    const [newName, setNewName] = useState('');
    const navigate = useNavigate()

    useEffect(() => {
        const token = localStorage.getItem('token');
        if (!token) {
            navigate('/login'); //    ,   
        } else {
            const headers = {
                Authorization: `Bearer ${token}`,
            };
            axios.get(PROF, {headers})
                .then(response => {
                    setUserData(response.data);
                })
                .catch(error => {
                    console.error('Error fetching user data:', error);
                });
        }
    }, []);

    const handleEditClick = () => {
        setEditMode(true);
        setNewName(userData.nick);
    };

    const handleNameChange = (event) => {
        setNewName(event.target.value);
    };

    const handleNavigateToFriends = () => navigate('/profile/myFriends')
    const handleExit = () =>{
        navigate('/login')
        localStorage.removeItem('token');
    };

    const handleNavigateToOrders = () => {
        navigate('/profile/incidents')
    };
    const handleGetTask = () => {
        navigate('/profile/moving/task')
    };
    const handleNavigateToQuestionnaire = () =>{
        navigate('/profile/questionnaire')
    };

    const handleMessages = () =>{
        navigate('/profile/dialogs')
    };
    const handleSaveClick = () => {
        const token = localStorage.getItem('token');
        const headers = {
            Authorization: `Bearer ${token}`,
        };
        axios.post(PROF, { name: newName }, { headers })
            .then(response => {
                setUserData(response.data);
                setEditMode(false);
                axios.get(PROF, { headers })
                    .then(response => {
                        setUserData(response.data);
                    })
                    .catch(error => {
                        console.error('Error fetching updated user data:', error);
                    });
            })
            .catch(error => {
                console.error('Error updating user data:', error);
            });
    };

    return (
        <div>
        <div className={classes.friendsContainer}>
            {userData && (
                <div>
                <div>
                    <div>
                        <Typography className={classes.userInfo}>
                         : {editMode ?
                        <TextField
                            value={newName}
                            onChange={handleNameChange}
                            variant="outlined"
                            size="small"
                        /> :
                        userData.nick
                    }
                    </Typography>
                    <Typography className={classes.userInfo}>: {userData.countFriends}</Typography>
                        {localStorage.getItem('role') === "" ?(
                    <Typography className={classes.userInfo}>: {userData.skill}</Typography>
                        ):null}
                    </div>
                    <div>{!editMode && (
                        <IconButton onClick={handleEditClick}>
                            <EditIcon />
                        </IconButton>
                    )}
                    {editMode && (
                        <Button onClick={handleSaveClick} className={classes.returnButton} style={{ width: 200, padding: 8 }}>
                            
                        </Button>
                    )}
                    </div>

                </div>
                </div>
            )}
        </div>
            <div style={{ display: 'flex', justifyContent: 'center', marginTop:'50px'}}>
                <div className={classes.listItem} style = {{marginRight:'20px', marginLeft:'20px'}}>
                    <Button onClick={handleNavigateToFriends} className={classes.returnButton} style={{ width: 200, padding: 8 }}>
                          
                    </Button>
                </div>
                <div className={classes.listItem} style = {{marginRight:'20px', marginLeft:'20px'}}>
                    <Button onClick={handleNavigateToOrders} className={classes.returnButton} style={{ width: 200, padding: 8 }}>
                          
                    </Button>
                </div>
                {localStorage.getItem('role') === "" ?(
                <div className={classes.listItem} style = {{marginRight:'20px', marginLeft:'20px'}}>
                    <Button onClick={handleGetTask} className={classes.returnButton} style={{ width: 200, padding: 8 }}>
                         
                    </Button>
                </div>
                ):null}
                    {localStorage.getItem('role') === "" ?(
                <div className={classes.listItem} style = {{marginRight:'20px', marginLeft:'20px'}}>
                    <Button onClick={handleNavigateToQuestionnaire} className={classes.returnButton} style={{ width: 200, padding: 8 }}>
                         
                    </Button>
                </div>
                    ):null}
                <div className={classes.listItem} style = {{marginRight:'20px', marginLeft:'20px'}}>
                    <Button onClick={handleMessages} className={classes.returnButton} style={{ width: 200, padding: 8 }}>
                         
                    </Button>
                </div>
                <div className={classes.listItem} style = {{marginRight:'20px', marginLeft:'20px'}}>
                    <Button onClick={handleExit} className={classes.returnButton} style={{ width: 200, padding: 8 }}>
                        
                    </Button>
                </div>
            </div>

        </div>


);
}

export default Profile;
