import React, {useEffect, useState} from 'react';
import Layout from "../../layout/Layout";
import {CircularProgress, IconButton} from "@mui/material";
import {makeStyles} from "@mui/styles";
import Typography from "@mui/material/Typography";
import TextInput from "../../stories/TextInput";
import {useNavigate} from "react-router-dom";
import CustomButton from "../../stories/CustomButton";
import ArrowBackIosIcon from '@mui/icons-material/ArrowBackIos';
import {connect} from "react-redux";
import {offRedirect, registration} from "../../service/reducer/actions";
import {checkRegistration} from "../../service/functions/validation";
import CatLayout from "../../layout/CatLayout";

const useStyles = makeStyles({
    mainBox: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        height: "calc(100vh - 60px)"
    },
    formBox: {
        background: "white",
        marginBottom: 100,
        width: "450px",
        "@media screen and (max-width:720px)": {
            width:"275px",
            marginBottom: 50,
        },
        padding: "20px",
        borderRadius: "20px",
        boxShadow: "5px 5px 20px gray",
    }
})

function Registration(props) {
    const [name, setName] = useState("")
    const [surname, setSurName] = useState("")
    const [login, setLogin] = useState("")
    const [password, setPassword] = useState("")
    const [repeatPassword, setRepeatPassword] = useState("")
    const [role, setRole] = useState('');
    const handleRoleChange = (selectedRole) => {
        setRole(selectedRole);
    };
    const [secretCode, setSecretCode] = useState('');
    const classes = useStyles()
    const navigate = useNavigate();

    function handleRegistration() {
        props.registration({
            name: name,
            surname: surname    ,
            login: login,
            password: password,
            role: role,
            code: secretCode
        })
    }


    useEffect(() => {
        if (localStorage.getItem("token") !== null) {
            navigate("/profile")
        }
    }, [])

    useEffect(() => {
        if (props.isRedirect) {
            navigate("/login")
            props.offRedirect()
        }
    }, [props.isRedirect])

    return (
        <Layout>
            <div className={classes.mainBox}>
                <CatLayout>
                    <div className={classes.formBox}>
                        <IconButton onClick={() => navigate(-1)}><ArrowBackIosIcon sx={{color: "pink"}}/></IconButton>
                        <Typography align={"center"}
                                    style={{fontWeight: "bold", fontSize: 22, marginTop: 16}}></Typography>

                        <div style={{height: 80}}>
                            <TextInput min={3} max={20}
                                       help={"    3  20   "} p={16}
                                       value={name} setValue={setName} type={"text"} label={""}/>
                        </div>

                        <div style={{height: 80}}>
                            <TextInput min={3} max={20}
                                       help={"    3  20   "} p={16}
                                       value={surname} setValue={setSurName} type={"text"} label={""}/>
                        </div>

                        <div style={{height: 80}}>
                            <TextInput min={6} max={20}
                                       help={"    6  20   "} p={16}
                                       value={login} setValue={setLogin} type={"text"} label={""}/>
                        </div>
                        <div style={{height: 80}}>
                            <TextInput password min={6} max={20}
                                       help={"    6  20   "} p={16}
                                       value={password} setValue={setPassword} type={"password"} label={""}/>
                        </div>
                        <div style={{height: 80}}>
                            <TextInput password p={16}
                                       value={repeatPassword} setValue={setRepeatPassword} type={"password"}
                                       label={" "}/>
                        </div>

                        <div>
                            <div>
                                <input
                                    type="radio"
                                    id=""
                                    name="role"
                                    value=""
                                    checked={role === ""}
                                    onChange={() => handleRoleChange("")}
                                />
                                <label htmlFor=""></label>
                            </div>
                            <div>
                                <input
                                    type="radio"
                                    id=""
                                    name="role"
                                    value=""
                                    checked={role === ""}
                                    onChange={() => handleRoleChange("")}
                                />
                                <label htmlFor=""></label>
                            </div>
                            <div>
                                <input
                                    type="radio"
                                    id="_"
                                    name="role"
                                    value="_"
                                    checked={role === "_"}
                                    onChange={() => handleRoleChange("_")}
                                />
                                <label htmlFor="_"> </label>
                            </div>
                        </div>
                        {role === "_" && (
                            <div style={{height: 80}}>
                                <TextInput
                                    p={16}
                                    value={secretCode} setValue={setSecretCode} type={"text"}
                                    label={" "}
                                />
                            </div>
                        )}


                        <div style={{display: "flex", justifyContent: "flex-end", marginTop: 32}}>
                            <CustomButton w={200} disabled={checkRegistration(login, password, repeatPassword, role, secretCode)}
                                          action={handleRegistration} p={8} title={props.fetchingReg ?
                                <CircularProgress style={{color: "pink", width: 20, height: 20}}/> :
                                ""
                            }/>
                        </div>
                    </div>
                </CatLayout>
            </div>
        </Layout>
    );
}

const mapStateToProps = (state) => ({
    ...state.app
})

export default connect(mapStateToProps, {
    registration,
    offRedirect
})(Registration);